.nav{
    position:fixed;
}

.link {
    color: black;
    padding: 2em;
}

.link:hover, .link-active:hover {
    color: black;
    background: #D8D7D7;
    padding: 2em;
    text-decoration: none; 
    
}

  /* Specific styles for active links */
.link-active{
    color: white;
    background: #1F1D1D;
    padding: 2em;
}

.bgcolor {
    height: 7vh;
    overflow: hidden;
    background-color:floralwhite;
    opacity: 0.98;
    background-position: absolute;
    background-image: url(../../images/ned.jpg);
    background-repeat: repeat;/* Determine what direction the image will repeat */
}

.navbar-brand {
    width: 40px;
    height: 6vh;
    align-items: center;
    justify-content: center;
    top: 0;
}

.nav-img {
    max-width:100%;
    max-height: fit-content;
    overflow: hidden;
}

.nav-img:hover{
    src: url(../../images/logoWA.jpg);
}
